import { MinusCircleOutlined } from "@ant-design/icons";
import { Card, Col, Row } from "antd";

const CDetailAssetImage = ({ image, imageUrl, isImageDua, imageUrlDua }) => {
  return (
    <Row style={{ width: "100%" }}>
      <Col span={12}>
        <Col
          style={{
            textAlign: "center",
            fontWeight: "bold",
            marginBottom: 5,
          }}
        >
          Original Photo
        </Col>
        <Col
          span={24}
          style={{
            display: "flex",
            justifyContent: "center",
            justifyItems: "center",
            alignItems: "center",
            marginBottom: 10,
          }}
        >
          {image ? (
            <Card
              style={{
                borderStyle: "none",
              }}
            >
              <img src={imageUrl} style={{ width: "100%" }} />
            </Card>
          ) : (
            <Card
              style={{
                backgroundColor: "#d3d3d3",
                height: 120,
                width: 120,
                textAlign: "center",
                display: "flex",
                justifyContent: "center",
                justifyItems: "center",
                alignItems: "center",
                fontSize: 30,
                borderStyle: "dotted",
                borderRadius: 5,
              }}
            >
              <MinusCircleOutlined />
            </Card>
          )}
        </Col>
      </Col>
      <Col span={12}>
        <Col
          style={{
            textAlign: "center",
            fontWeight: "bold",
            marginBottom: 5,
          }}
        >
          Last Photo
        </Col>
        <Col
          span={24}
          style={{
            display: "flex",
            justifyContent: "center",
            justifyItems: "center",
            alignItems: "center",
            marginBottom: 10,
          }}
        >
          {isImageDua ? (
            <Card
              style={{
                borderStyle: "none",
              }}
            >
              <img src={imageUrlDua} style={{ width: "100%" }} />
            </Card>
          ) : (
            <Card
              style={{
                backgroundColor: "#d3d3d3",
                height: 120,
                width: 120,
                textAlign: "center",
                display: "flex",
                justifyContent: "center",
                justifyItems: "center",
                alignItems: "center",
                fontSize: 30,
                borderStyle: "dotted",
                borderRadius: 5,
              }}
            >
              <MinusCircleOutlined />
            </Card>
          )}
        </Col>
      </Col>
    </Row>
  );
};

export default CDetailAssetImage;
